// Responsiveness
// Extra Small Devices (Phones)
@media only screen and (max-width: 575.98px) {

    // General
    div {

        div.pc-hidden,
        h3.pc-hidden {
            display: flex;
        }
    }

    ::-webkit-scrollbar {
        display: none;
    }

    label {
        width: 100%;
        max-width: 100%;
    }

    .row {
        .column {
            @include fa;
            justify-content: space-between;
            width: 100%;
        }
    }

    // Components
    .landing-banner {
        .get-started {
            margin: 0px;
        }

        width: 100dvw;
        @include fda;

        .banner-left {
            height: max-content;
        }

        .banner-left {
            align-items: center;

            h2 {
                font-size: 1.4rem;
            }

            p {
                font-size: 1rem;
            }
        }

        .banner-right.phone-hidden {
            display: none;
        }

        .banner-right.pc-hidden {
            @include fja;
            align-items: center;
            width: calc(100dvw - 50px);
            height: calc(100% - 40px);

            .student-image {
                max-height: calc(100% - 40px);
            }

