            .gamification-header,
            .left,
            .right {
                width: calc(100% - 30px);
                letter-spacing: normal;
            }

            .left {
                .large-text {
                    font-size: 1.3rem;
                }
            }

            .gamification-signup {
                width: 100%;
                @include fda;

                .button {
                    width: 100%;
                }
            }
        }
    }

    // Components - Page Right Header
    .page-right-header {
        .page-right-header-a {
            width: 100vw;
            padding: 0px;

            svg {
                display: none;
            }
        }

        .page-right-header-b {
            display: none;
            width: 100%;

            h3,
            .progress-status>p {
                display: none;
            }
        }
    }

    // Components - Dashboard Calendar
    .dashboard-calendar {
        .todays-tasks {
            align-items: flex-start;
        }
    }

    // Components - Dashboard Groups
    .dashboard-groups {
        padding: 0px;
    }

    // Components Dashboard Boxes
    .dashboard-boxes {
        grid-template-columns: auto;
    }

    // Components - Dashboard Tasks
    .dashboard-t-o-t-header.row {
        display: none;
    }

    .table {
        gap: 20px;

        .row {
            flex-direction: column;
            gap: 20px;
            height: max-content;
            border: solid 1px rgba(0, 24, 12, 0.2);
            border-radius: 5px;
            width: calc(100% - 33px);
            padding: 15px;
        }
    }


    // Pages
    .pages {
        overflow-x: hidden;
        .side-menu,
        .dashboard-ad-background // .page-right-header-b

            {
            display: none;
        }

        .page-right {
            width: 100%;
            position: absolute;
            left: 0px;

            .page-right-header-a {
                p {
                    display: none;
                }
            }
        }
    }

    // Pages - Dashboard
    .dashboard-page {
        .page-right-header .page-right-header-b {
            display: flex;
            margin: 0px;
        }
    }

    .dashboard-main-section {
        grid-template-areas:
            "dashboardbox dashboardbox"
            "dashboardbox dashboardbox"
            "dashboardcalendar dashboardcalendar"
            "classandexamschedule classandexamschedule"
            "dashboardtasks dashboardtasks"
            "dashboardgroups dashboardgroups";
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: auto;

        .dashboard-box {
            border-radius: 2px;
        }

        p {
            font-size: 1.05rem;
        }

        .dashboard-calendar {
            font-size: 1rem;
        }
    }

    // Pages - Landing Page
    .landing-page {

        h1,
        h2,
        h3,
        h4,
        h5,
        h6,
        p {
            text-align: center;
        }
    }

    .app-features {
        .heading {
            font-size: 1.5rem;
        }

        .app-feature {
            display: grid;
            grid-template-areas:
                "top"
                "bottom";
            grid-template-columns: auto;

            .left {
                grid-area: bottom;
                border-right: 0px;
            }

            .right {
                grid-area: top;
            }
        }

        .app-feature.app-feature-gamification {
            @include fda;
        }
    }

    // Pages - About-page
    .about-page {
        .header-text {
            font-size: 1.5rem;
        }

        p {
            font-size: 1rem
        }
    }

    .about-banner {
        @include fda;
        height: max-content;

        h1 {
            font-size: 1.5rem;
        }

        .banner-left {
            p {
                font-size: 1rem;
            }
        }
    }

    .about-edvantage {
        grid-template-areas:
            "whoweareimage"
            "whowearedescrip"
            "whatdrivesusimage"
            "whatdrivesusdescrip";
        grid-template-columns: auto;
    }

    .key-features {
        margin-top: 20px;

        .features {
            grid-template-areas:
                "feature1"
                "feature2"
                "feature3"
                "feature4";
        }
    }

    .work-with-us {
        .card {
            grid-template-areas:
                "workwithusimage"
                "workwithusdescrip";
            grid-template-columns: auto;
        }
    }

    // Pages - Assignment
    .assignments-page {
        .table {
            width: 90%;
        }

        .assignment-header.row {
            display: none;
        }
    }

    // Pages - Chat Page
    .whole-chat-section {
        grid-template-columns: auto;
        height: calc(100dvh);
        border-top: 0px;

        .left {
            width: 100%;
            height: calc(100dvh - $page-right-header-height);

            .chat-profiles {
                border: 0px;
            }
        }

        .right {
            margin: 0px;
            height: 100%;

            .chat-input-box {
                width: calc(100dvw - 20px);
                gap: 10px;
            }

            .chat-header {
                gap: 10px;

                svg {
                    margin: 0px 0px 0px 20px;
                    display: block;
                }
            }
        }

        &.show-chat {
            .left {
                display: none;
            }

            .right {
                display: flex;
                height: calc(100dvh - $page-right-header-height);
                width: 100%;
                margin: 0px;

                .chat-input-box {
                    width: calc(100dvw - 20px);
                    gap: 10px;
                }
            }
        }

        .add-new-chat-profile {
            width: 80dvw;
        }
